import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import ModuleTable from './Tables/ModuleTable';
import ModuleModal from './Modals/ModuleModal';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import DepartmentSearch from '../../SearchComponents/DepartmentSearch/DepartmentSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class Modules extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            filtereddepartmentData: [],
            value: '',
            show: false,
            showdelete: false,
            MODULE_ID: 0,
            MODULE_NAME: '',
            SYS_STATUS_ID: 0,
            SET_DEPARTMENT_ID: 0,
            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            opendepartmentsearch: false,
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            DEPARTMENT_NAME: '',
            searchcolumns: [],
            searchdata: [],
            searchdepartmentdata: [],
            searchFilteredData: [],
            searchvalue: '',
            searchdepartmentvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };
        this.handleDepartmentSearch = () => {
            this.setState({ opendepartmentsearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.handleHideDepartmentSearch = () => {
            this.setState({ opendepartmentsearch: false });
        };
        this.populateData();
        this.populateTypes();
        this.populateDepartments();
    }
  
    populateData() {
        const getdata = `
            query{
                Modules{
                    MODULE_ID
                    MODULE_NAME
                    SET_DEPARTMENT_ID
                        SET_DEPARTMENTS{
                        DEPARTMENT_ID
                        DEPARTMENT_NAME
                        }
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.Modules
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })
    }

    populateTypes() {
        
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    populateDepartments() {
        
        const getdata = `
            query {
                Departments{
                        DEPARTMENT_ID,
                        DEPARTMENT_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdepartmentdata: data.data.Departments
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Department Load Data');
                }
            })
    }


    handleSubmit = (event) => {
        event.preventDefault();
        
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            const form = `
        mutation{
            addModule(
                MODULE_NAME:"${this.state.MODULE_NAME}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    MODULE_ID
            }
        }
        `;
            
            const formEdit = `
        mutation{
            updateModule(
                MODULE_ID:${this.state.MODULE_ID},
                MODULE_NAME:"${this.state.MODULE_NAME}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    MODULE_ID
            }
        }
        `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();
        
        const formDelete = `
        mutation{
            deleteModule(
                MODULE_ID:${this.state.MODULE_ID}
                ){
                    MODULE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });
        
    };


    handleCloseDepartmentSearch = (e) => {

        this.setState({
            DEPARTMENT_ID: e.DEPARTMENT_ID,
            DEPARTMENT_NAME: e.DEPARTMENT_NAME,
            opendepartmentsearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            MODULE_ID: e.MODULE_ID,
            MODULE_NAME: e.MODULE_NAME,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SET_DEPARTMENT_ID: e.SET_DEPARTMENT_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            DEPARTMENT_NAME: e.SET_DEPARTMENTS.DEPARTMENT_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            MODULE_ID: e.MODULE_ID,
            MODULE_NAME: e.MODULE_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            MODULE_NAME: '',
            SYS_STATUS_ID: '',
            SET_DEPARTMENT_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            validated: false,
            DEPARTMENT_NAME: ''
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchDepartmentFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdepartmentdata
        let filtereddepartmentData = this.state.searchFilteredData
        this.setState({ searchdepartmentvalue: value });

        if (value.length) {
            filtereddepartmentData = data.filter(item => {
                let startsWithCondition =
                    item.DEPARTMENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.DEPARTMENT_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filtereddepartmentData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
    
    render() {
        const { data, value, filteredData, filtereddepartmentData, searchdata, searchdepartmentdata, searchvalue, searchdepartmentvalue } = this.state
        return (
            <>
                <Helmet><title>Modules</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Modules"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Modules</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <DepartmentSearch
                                        handleSearchDepartment={this.handleCloseDepartmentSearch}
                                        opensearch={this.state.opendepartmentsearch}
                                        handleHideSearch={this.handleHideDepartmentSearch}
                                        value={searchdepartmentvalue}
                                        data={searchdepartmentdata}
                                        handleFilter={this.handleSearchDepartmentFilter}
                                        filteredData={filtereddepartmentData}
                                    />


                                    <ModuleTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <ModuleModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}
                                        handleDepartmentSearch={this.handleDepartmentSearch}
 
                                        DEPARTMENT_NAME={this.state.DEPARTMENT_NAME}
                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        MODULE_NAME={this.state.MODULE_NAME}
                                        handleChangeModuleName={e => this.setState({ MODULE_NAME: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(Modules));